/* --- direction list for indexOf function --- */
[data-tooltip-direction="top"] {
  position: relative;
  cursor: pointer; }
  [data-tooltip-direction="top"]::before {
    margin-bottom: 5px; }
  [data-tooltip-direction="top"]::after {
    margin-bottom: 10px; }
  [data-tooltip-direction="top"]::before, [data-tooltip-direction="top"]::after {
    /* --- base styles --- */
    display: inline-block;
    opacity: 0;
    -webkit-transition: opacity .2s ease-in-out;
    -o-transition: opacity .2s ease-in-out;
    transition: opacity .2s ease-in-out;
    z-index: 1070;
    /* --- position --- */
    position: absolute;
    bottom: 100%;
    left: 50%;
    -webkit-transform: translateX(-50%);
        -ms-transform: translateX(-50%);
         -o-transform: translateX(-50%);
            transform: translateX(-50%); }
  [data-tooltip-direction="top"]::after {
    content: attr(data-tooltip-content);
    padding: 5px 10px;
    font-size: 12px;
    color: #fff;
    background-color: rgba(0, 0, 0, 0.9);
    border-radius: 3px;
    padding: .5em 1em;
    white-space: nowrap; }
  [data-tooltip-direction="top"]::before {
    content: '';
    border-top: 5px solid rgba(0, 0, 0, 0.9);
    border-left: 5px solid transparent;
    border-right: 5px solid transparent; }
  [data-tooltip-direction="top"]:hover::before, [data-tooltip-direction="top"]:hover::after {
    opacity: 1; }
  [data-tooltip-direction="top"][data-tip-edge]::after {
    border-radius: 0px; }

